.portfolio {
    .portfolioContent {
        width: 100%;
        display: grid;
        min-height: $minHeight;
        grid-template-columns: 1fr;
        grid-template-rows: 50px 1fr;
        grid-template-areas: 
            "R"
            "P"
        ;
        
        .radioDisplay{
            grid-area: R;
            border-radius: $borderRadius;
            display: flex;
            width: 100%;
            padding: 1rem 2rem;
            margin: 0 auto;
            background: $color-1;
            user-select: none;
            box-shadow: $shadow;

            li {
                color: $color-3;
                font-size: 1.1rem;
                text-transform: uppercase;
                padding: 0 1rem;
                flex: 1;
                z-index: 2;
                @include verticalCenter;
                
                input[type="radio"]{
                    visibility: hidden;
                    position: relative;

                    &::before {
                        content: '';
                        visibility: visible;
                        position: absolute;
                        top: -4px;
                        left: -12px;
                        height: .9rem;
                        width: .9rem; 
                        border-radius: 50%;
                        border: 2px solid $color-4;
                        cursor: pointer;
                        transition: .3s;
                    }

                    &::after {
                        content: '';
                        visibility: visible;
                        position: absolute;
                        top: -4px;
                        left: -12px;
                        height: .9rem;
                        width: .9rem;
                        border-radius: 50%;
                        cursor: pointer;
                        transition: .3s;
                        background: $color-4;
                        transform: scale(0);
                        transform-origin: center;
                    }
                }

                input:checked:after {
                    transform: scale(1.17) translate(-25%, -25%);
                    top: 2px;
                    left: -6px;
                }

                input:checked + label {
                    color: $color-4;
                }

                label {
                    cursor: pointer;
                }
            }

        }

        .projects {
            grid-area: P;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;

            .project {
                background: $color-2;
                border-radius: $borderRadius;
                margin: .5rem 0 0 0;
                text-align: center;
                width: 32%;
                max-height: 50%;
                padding: .3rem;
                box-shadow: $shadow;
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: 15% 15% 55% 15%;
                grid-template-areas: 
                    "1"
                    "2"
                    "3"
                    "4"
                ;

                .icons {
                    grid-area: 1;
                    display: flex;
                    justify-content: center;

                    i {
                        font-size: 1.4rem;
                        padding: 0 .35rem;
                        color: $color-4;
                        text-shadow: 0 0px 1px $color-1;
                    }
                }

                h3 {
                    grid-area: 2;
                    font-family: $font-2;
                    font-size: 1.65rem;
                    justify-content: left;
                }

                img {
                    grid-area: 3;
                    object-fit: cover;
                    margin: 0 auto;
                    width: 100%;
                    height: 100%;
                    box-shadow: 0 2px 6 px $black;
                    border-radius: 4px;
                   // @include verticalCenter;
                }

                .infos {
                    grid-area: 4;
                    color: $color-4;
                    font-size: 1.5rem;
                    transition: .2s;
                    padding-top: 0.8rem;

                    &:hover {
                        transform: scale(1.1);
                    }
                }

                .showInfos {
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    right: 0;
                    background: rgba(0, 0, 0, 0.507);
                    animation: popup .3s ease forwards;
                    transform-origin: center;

                    .infoContent {
                        position: relative;
                        top: 25%;
                        width: 600px;
                        margin: 0 auto;
                        color: $color-5;
                        background: $color-1;
                        border-radius: $borderRadius;
                        padding: 2rem;
                        box-shadow: 0 10px 10px $black;
                        animation: popup .6s ease forwards;
                        transform: scale(0);
                        transform-origin: center;

                        @keyframes popup {
                            to {
                                transform: scale(1);
                            }
                        }
                    }

                    .head {
                        display: flex;
                        justify-content: space-around;
                        margin: 1rem 0;
                    }

                    h2 {
                        text-align: left;
                        margin: 1rem;
                    }

                    .button {
                        cursor: pointer;
                        color: $color-1;
                        background: $color-5;
                        border-radius: $borderRadius;
                        padding: 1rem;
                        transition: .2s;

                        &:hover {
                            background: $color-4;
                        }
                    }

                    .text {
                        text-align: justify;
                        padding-bottom: 1rem;
                    }

                    .return {
                        margin: 1rem auto;
                        width: 200px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 850px){
    .portfolioContent {
        min-height: 830px !important;
    }
}

@media (max-width: 650px) and (min-height: 521px) {
    .portfolioContent{
        .projects{
            display: flex;
        
            .project {
                width: 49% !important;

                i {
                    font-size: 1rem;
                }
            }

            .infoContent {
                width: 100% !important;
            }
        }
    }
}

@media screen and (max-width: 500px) {
    .radioDisplay{
        min-width: 100% !important;
        
        li {
            font-size: .7rem !important;
        }
    }
    .portfolioContent{
        grid-template-rows: 1fr !important;
        .projects{
            .project {
                grid-template-rows: 30px 40px 190px 40px !important;
                width: 100% !important;
    
                img {
                    height: 100%!important;
                }
            }
        }
    }
}

